#AboutPage{
  text-align: center;
  min-height:100vh;
}

#AboutTitle{
  padding-top:3%;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 15px;
}


#IntroductionRow{
margin-top:5%;
}

#StrengthRowImage{
margin-top:5%;
}

#strengthTab{
  font-size:1.3rem;
  color: rgb(98,98,98);
}

#selfie{
  max-width: 60%;
}

#quote{
font-family: 'Kalam', cursive;
text-align: center;
}
.quotation {
	font-weight: 100;
	font-size: 1.5rem;
	max-width: 600px;
	line-height: 1.4;
	position: relative;
	margin: 0;
	padding: .5rem;
}

cite {
	line-height: 0.5;
	text-align: left;
}

#selfIntroText{
  text-align: left;
  font-family: 'Roboto', sans-serif;
  font-size:0.9rem;
}

.highlight{
background-color: rgb(49,148,201);
color:white;
}

.floating {
    animation-name: floating;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}
@keyframes floating {
    from { transform: translate(0,  0px); }
    65%  { transform: translate(0, 15px); }
    to   { transform: translate(0, -0px); }
}

/* text rotate effect */
.textRotate span {
  display: inline-block;
  transition: 0.8s;
}

.textRotate:hover span:nth-child(odd) {
  transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( 370deg );
  cursor: pointer;
}

.textRotate:hover span:nth-child(even) {
  transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( -370deg );
  cursor: pointer;
}

#strengthTab:hover{
text-decoration: none;
}


#StrengthCharts{
  font-size:0.7rem;
  padding-top: 5%;
  padding-bottom:5%;
}

#StrengthChartsDiv{
  margin-top:5%;
  min-height: 60vh;
  background-color: rgb(245,245,245);
}
.expandPageButton{
  transition: opacity 0.5s ease-in-out;
  opacity: 0.6;
}

.expandPageButton:hover{
opacity:1;
}

#resumeHint{
  font-size:1rem;
  transition:opacity 0.5s;
}

.rotate {
    -moz-transition: all .5s linear;
    -webkit-transition: all .5s linear;
    transition: all .5s linear;
}
.rotate.down {
    -moz-transform:rotate(180deg);
    -webkit-transform:rotate(180deg);
    transform:rotate(180deg);
}

#expandButton{
  transition: background-color 0.5s ;
}
