@-webkit-keyframes floating {
    from { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    65%  { -webkit-transform: translate(0, 15px); transform: translate(0, 15px); }
    to   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }
}
@keyframes floating {
    from { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    65%  { -webkit-transform: translate(0, 15px); transform: translate(0, 15px); }
    to   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }
}

/* text rotate effect */
.textRotate span {
  display: inline-block;
  -webkit-transition: 0.8s;
  transition: 0.8s;
}

.textRotate:hover span:nth-child(odd) {
  -webkit-transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( 370deg );
          transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( 370deg );
  cursor: pointer;
}

.textRotate:hover span:nth-child(even) {
  -webkit-transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( -370deg );
          transform: rotateX( 0deg ) rotateY( 0deg ) rotateZ( -370deg );
  cursor: pointer;
}

